@import url(../../node_modules/normalize.css/normalize.css);

html {
  position: relative;
  min-height: 100%;
  font-size: 16px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Ubuntu", Helvetica, Arial, sans-serif;
  margin: 0;
  padding: 0;
  width: 100%;
  min-height: 100%;
}

img,
embed,
object,
video {
  max-width: 100%;
}

ul {
  list-style-type: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

.wrapper {
  position: relative;
  min-height: 100%;
  overflow: hidden;
}

.container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 940px;
  height: 100%;
  margin: 0 auto;
}

.section {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}

.title {
  font-family: "Gagalin", Helvetica, Arial, sans-serif;
  color: #f9b43b;
  font-size: 4.5rem;
  margin-bottom: 20px;
}

.title_color-black {
  color: #1f2122;
}

.title_color-white {
  color: #ffffff;
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/Ubuntu-Light.woff2") format("woff2");
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Ubuntu-Regular.woff2") format("woff2");
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Ubuntu-Medium.woff2") format("woff2");
}

@font-face {
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Ubuntu-Bold.woff2") format("woff2");
}

@font-face {
  font-family: "Lifehack";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/Lifehack-Medium.woff2") format("woff2");
}

@font-face {
  font-family: "Gagalin";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Gagalin-Regular.woff2") format("woff2");
}

.accordeon {
  max-width: 24rem;
}

.accordeon__item {
  margin-bottom: 2rem;
}

.accordeon__item_active .employee__wrapper {
  display: flex;
  height: 6.25rem;
}

.accordeon__item_active .employee__link::before {
  transform: rotate(180deg);
}

.accordeon__item_active .employee__title {
  color: #f9b43b;
}

.arrow-scroll {
  position: absolute;
  left: 50%;
  bottom: 2.5rem;
}

.arrow-scroll__icon {
  width: 1.812rem;
  height: 0.875rem;
}

.best {
  background: url(../img/bg-best.jpg) center/cover no-repeat;
}

.best__list {
  display: flex;
  margin-top: 2.4375rem;
}

.best__item {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 10px;
}

.best__desc {
  text-align: center;
}

.best__title {
  font-family: "Lifehack", Helvetica, Arial, sans-serif;
  font-size: 1.5rem;
  color: #1f2122;
  margin-bottom: 1.25rem;
}

.best__text {
  font-size: 14px;
  font-weight: 300;
  color: #1f2122;
  line-height: 1.8;
  opacity: .8;
}

.best__img {
  margin-bottom: 2.1875rem;
}

.best__icon {
  max-width: 96px;
  max-height: 89px;
}

.button {
  font-size: 0.8125rem;
  font-weight: 400;
  color: #ffffff;
  padding: 0.625rem 0.9375rem;
  border-radius: 0.3125rem;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #e45028;
  transition: background-color 0.15s ease;
}

.button:hover {
  background-color: #c1401e;
}

.employee {
  color: #ffffff;
}

.employee__wrapper {
  overflow: hidden;
  height: 0rem;
  transition: height 0.15s ease;
  will-change: height;
}

.employee__link {
  display: flex;
  align-items: center;
  color: #ffffff;
  font-family: "Gagalin", Helvetica, Arial, sans-serif;
  text-decoration: none;
  font-size: 0.875rem;
  margin-bottom: 0.9375rem;
}

.employee__link::before {
  content: '';
  border: 0.4rem solid transparent;
  border-top-color: #f9b43b;
  border-bottom-width: 0;
  margin-right: 0.4rem;
  transition: transform 0.15s ease;
}

.employee__content {
  display: flex;
  padding-left: 1rem;
}

.employee__img {
  min-width: 6.25rem;
  min-height: 6.25rem;
  background-size: cover;
  background-position: center;
  border-radius: 50%;
}

.employee__title {
  transition: color 0.15s ease;
}

.employee__desc {
  margin-left: 1rem;
}

.employee__position {
  font-size: 0.875rem;
  font-weight: 700;
  line-height: 1.5rem;
  margin-bottom: 0.5rem;
}

.employee__text {
  font-weight: 300;
  line-height: 1.5;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  top: 1rem;
  right: 0;
  left: 0;
}

.header__nav {
  display: flex;
}

.hero {
  background: url(../img/bg-hero.jpg) center/cover no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.hero__container {
  display: flex;
}

.hero__content {
  display: flex;
  align-items: center;
}

.hero__text {
  display: flex;
  flex-direction: column;
  flex-basis: 50%;
  line-height: 1;
}

.hero__picture {
  flex-basis: 50%;
  margin-right: 1rem;
}

.hero__description {
  font-family: "Lifehack", Helvetica, Arial, sans-serif;
  font-size: 4.062rem;
  color: #ffffff;
  line-height: 1;
  letter-spacing: -1.63px;
}

.hover {
  display: flex;
  align-items: center;
  height: 100%;
  background: rgba(69, 69, 69, 0.7);
  opacity: 0;
  transition: opacity 0.15s ease;
}

.hover__content {
  padding: 0 1.25rem;
  color: #ffffff;
}

.hover__title {
  font-family: "Gagalin", Helvetica, Arial, sans-serif;
  font-size: 0.875rem;
  line-height: 1.75rem;
  margin-bottom: 1.25rem;
}

.hover__text {
  font-size: 0.875rem;
  font-weight: 300;
  line-height: 1.5rem;
  margin-bottom: 1.25rem;
  padding-right: 1rem;
}

.hover__button {
  background: #1f2122;
  text-align: center;
  padding: 0.8125rem 0.9375rem;
  border-radius: 0.1875rem;
  box-sizing: border-box;
  color: #ffffff;
  font-size: 0.8125rem;
  text-transform: uppercase;
  text-decoration: none;
  transition: background-color 0.15s ease;
}

.hover__button:hover {
  background-color: #1f2122;
}

.hover__button-mobile {
  display: none;
}

.logo {
  display: block;
}

.logo__icon {
  width: 5.75rem;
  height: 4.3125rem;
}

.menu-acc {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  height: 100%;
}

.menu-acc__list {
  display: flex;
}

.menu-acc__item {
  display: flex;
}

.menu-acc__item_active .menu-acc__desc {
  display: flex;
  width: 33.75rem;
}

.menu-acc__item_active .menu-acc__title {
  color: #f9b43b;
}

.menu-acc__cover {
  font-size: 2.5rem;
  font-family: "Gagalin", Helvetica, Arial, sans-serif;
  color: #ffffff;
  width: 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-position: center;
  position: relative;
}

.menu-acc__cover::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(20, 20, 20, 0.7);
}

.menu-acc__title {
  transform: rotate(-90deg);
  width: 90vh;
  position: absolute;
  transition: color 0.3s ease;
}

.menu-acc__desc {
  background: rgba(47, 50, 52, 0.93);
  width: 0;
  overflow: hidden;
  transition: width 0.3s ease;
}

.menu-acc__text {
  padding: 3.75rem 2.1875rem;
  color: #ffffff;
  font-size: 0.875rem;
  line-height: 1.714;
  min-width: 33.75rem;
}

.menu {
  background: url(../img/bg-menu.jpg) center/cover no-repeat;
  position: relative;
}

.menu__title {
  color: #ffffff;
  position: absolute;
  top: 20%;
  left: 10%;
}

.nav {
  display: flex;
  align-items: center;
  margin-right: 0.9375rem;
}

.nav__list {
  display: flex;
}

.nav__item {
  display: flex;
  align-items: center;
  margin-right: 0.928571rem;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__item::after {
  content: '';
  width: 5px;
  height: 5px;
  background-color: #ffffff;
  border-radius: 50%;
  margin-left: 0.9375rem;
}

.nav__item:last-child::after {
  display: none;
}

.nav__link {
  font-family: "Ubuntu", Helvetica, Arial, sans-serif;
  font-weight: 500;
  font-size: 0.8125rem;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  transition: color 0.15s ease;
}

.nav__link:hover {
  color: #c1401e;
}

.pagination {
  position: fixed;
  right: 3.125rem;
  top: 6.25rem;
  z-index: 2;
}

.pagination__list {
  display: flex;
  flex-direction: column;
}

.pagination__item {
  margin-bottom: 5px;
}

.pagination__item_active .pagination__link {
  border: 1px solid #ffffff;
}

.pagination__link {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid transparent;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  transition: border 0.15s ease;
}

.pagination__link::after {
  content: '';
  display: flex;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: #ffffff;
}

.pagination__link:hover {
  border: 1px solid #ffffff;
}

.reviews {
  position: relative;
}

.reviews__list {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
}

.reviews__item {
  position: relative;
  flex-basis: 25%;
  width: 25%;
  height: 50vh;
  background-size: cover;
  background-position: center;
}

.reviews__item:hover .hover {
  opacity: 1;
}

.team {
  background: url(../img/bg-team.jpg) center/cover no-repeat;
  position: relative;
}

.team__content {
  background: rgba(47, 50, 52, 0.93);
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 50%;
  display: flex;
  flex-direction: column;
  padding: 6% 0 0 9%;
}

.team__title {
  margin-bottom: 2.8125rem;
}

.mobile {
  display: none;
  align-items: center;
  margin-left: 2.1428rem;
}

.mobile__icon {
  fill: #ffffff;
  width: 1.7142rem;
  height: 1.5714rem;
}

@media (max-width: 768px) {
  html {
    font-size: 14px;
  }

  .container {
    width: 95%;
  }

  .accordeon__item_active .employee__wrapper {
    flex-direction: column;
    height: 13.25rem;
  }

  .button {
    font-size: .75rem;
  }

  .employee {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .employee__link {
    margin-bottom: 2rem;
  }

  .employee__content {
    padding-left: 0;
    flex-direction: column;
    text-align: center;
    align-items: center;
  }

  .employee__img {
    margin-bottom: 1.571428571428571rem;
  }

  .employee__title {
    font-size: 1.785714285714286rem;
  }

  .employee__desc {
    margin-left: 0;
  }

  .hero__content {
    flex-direction: column;
    text-align: center;
    width: 95%;
  }

  .hero__picture {
    margin: 0;
  }

  .hero__description {
    padding: 0 5rem;
  }

  .logo {
    z-index: 20;
  }

  .nav {
    display: none;
    justify-content: center;
    align-items: center;
    display: none;
    background: rgba(47, 50, 52, 0.99);
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10;
  }

  .nav__list {
    position: absolute;
    display: flex;
    flex-direction: column;
  }

  .nav__item {
    display: flex;
    flex-flow: column nowrap;
    margin: 0;
  }

  .nav__item::after {
    margin-bottom: 1.7857rem;
    margin-left: 0;
  }

  .nav__link {
    font-size: 1.7142rem;
    font-weight: 400;
    margin-bottom: 1.7857rem;
  }

  .reviews__item {
    flex-basis: 50%;
    width: 50%;
    height: 25vh;
  }

  .team::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(47, 50, 52, 0.93);
  }

  .team__content {
    width: 100%;
    background: none;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 0;
    align-items: center;
    z-index: 1;
  }

  .mobile {
    display: flex;
  }
}

@media (max-width: 480px) {
  html {
    font-size: 13px;
  }

  .best__list {
    flex-direction: column;
  }

  .best__item {
    flex-direction: row;
    margin-bottom: 3.846153846153846rem;
  }

  .best__desc {
    text-align: left;
  }

  .best__img {
    margin-bottom: 0;
    margin-right: 2rem;
  }

  .hover__content {
    display: flex;
    flex-direction: column;
  }

  .hover__title {
    font-size: 1.846153846153846rem;
    text-align: center;
    margin-bottom: 2.307692307692308rem;
  }

  .hover__text {
    display: none;
  }

  .hover__button {
    display: none;
  }

  .hover__button-mobile {
    display: inline-block;
    align-self: center;
    background: #1f2122;
    padding: 0.92rem 1.2rem;
    border-radius: 0.35rem;
    color: #ffffff;
    font-size: 1rem;
    text-transform: uppercase;
    text-decoration: none;
  }

  .logo__icon {
    width: 7.076923076923077rem;
    height: 5.307692307692308rem;
  }
}